"use babel";

import { Inkdrop } from "./types";

declare const inkdrop: Inkdrop;

class Settings {
    ApiKey: string = "";

    constructor() {
        inkdrop.config.observe("chess_rabbit.ApiKey", (newValue: string) => {
            this._changeApiKey(newValue);
        });
    }
    /*
     *
     */
    refresh() {}
    _changeApiKey(newValue: string) {
        if (!newValue) {
            return;
        }
    }
}

export default new Settings();
